.ProductDetails {
  background-color: rgb(255, 255, 255);
  width: 120vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: block;
  flex-direction: column;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}
.CarouselImage {
    display: block;
    margin: auto;
  width: 25vmax;
  height: 100%;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.6vmax "Roboto";
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.959);
  font: 200 0.8vmax cursive;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.466);
  border-bottom: 1px solid rgba(0, 0, 0, 0.596);
  margin: 1.2vmax 0;
  width: 70%;
  padding: 1vmax 0;
}
/* .detailsBlock-2 > span {
  color: rgba(54, 54, 54, 0.918);
  font: 200 0.8vmax cursive;
} */

.detailsBlock-2-span {
    font: 200 0.8vmax cursive;
    color: rgba(0, 0, 0, 0.699);
  }

.detailsBlock-3 {
  width: 100%;
}

.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
}

.detailsBlock-3-1 {
    display: flex;
    align-items: center;

}
.detailsBlock-3-1-1 > button{
 border: none;
 background-color: rgba(0, 0, 0, 0.788);
 padding: 0.5vmax;
 cursor: pointer;
 color: white;
 transition: all 0.5s;
}

.detailsBlock-3-1-1 >button:hover{
    background-color: rgba(0, 0, 0, 0.801);

}
.detailsBlock-3-1-1 >input{
    border: none;
    padding: 0.8vmax;
    width: 1vmax;
    text-align: center;
    outline: none;
    color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 >button:last-child{
    border: none;
    cursor: pointer;
    color: #000000;
    transition: all 0.5s;
    background-color: #fffc48;
    font: 500 0.9vmax "Roboto";
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none  ;
}

.detailsBlock-3-1 >button:last-child:hover{
    background-color: #e6e22e;
}

.detailsBlock-3 > p{
    border-top: 1px solid rgba(0, 0, 0, 0.486);
    border-bottom: 1px solid rgba(0, 0, 0, 0.507);
    padding: 1vmax 0;
    color: rgba(0,0,0,0.651);
    font: 400 1.2vmax "Roboto";
    margin: 1vmax 0;
    width: 70%;
}

.detailsBlock-4 {
    color: rgba(0, 0, 0, 0.897);
    font: 500 1.2vmax sans-serif;

}

.detailsBlock-4 > p{
    color: rgba(0, 0, 0, 0.658);
    font: 300 0.9vmax sans-serif;
}

.submitReview {
    border: none;
    background-color: #fffc48;
    font: 500 0.9vmax "Roboto";
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax 0;
    color: black;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
}

.submitReview:hover{
    background-color: #e6e22e;
    transform: scale(1.1);
}

.submitDialog {
    display: flex;
    flex-direction: column;
  }
  .submitDialogTextArea {
    border: 1px solid rgba(0, 0, 0, 0.082);
    margin: 1vmax 0;
    outline: none;
    padding: 1rem;
    font: 300 1rem "Roboto";
  }

.reviewHeading{
    color: #000000be;
    font: 500 1.4vmax "Roboto";
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax;
}

.reviews{
    display: flex;
    overflow: auto;
}

.reviewCard{
    flex: none;
    box-shadow: 0 0 5px rgba(0,0,0,0.226);
    border: 1px solid rgba(56, 56, 56, 0.116);
    width: 30vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 3vmax;
}
.reviewCard >img{
    width: 5vmax;
}

.reviewCard >p{
    color: rgba(0, 0, 0, 0.836);
    font: 600 0.9vmax "Roboto";
}

.reviewCardComment{
    color: rgba(0, 0, 0, 0.521);
    font: 300 0.9vmax cursive;
}
.noReviews{
    font: 400 1.7vmax "Gill Sans";
    text-align: center;
    color: rgba(0, 0, 0, 0.63);
}



@media screen and (max-width :600px){
    .ProductDetails {
        flex-direction: column;
        height: unset;
    }
    .ProductDetails > div:last-child{
       
        align-items: center;
    }
    .detailsBlock-1 > h2{
        font-size: 2.8vmax;
        text-align: center;
    }
    .detailsBlock-1 > p{
        font-size: 1.2vmax;
        text-align: center;
    }
    .detailsBlock-2 {
        justify-content: center;
    }
    .detailsBlock-2 >span {
        font-size: 1.5vmax;
    }
    .detailsBlock-3 >p{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: auto;
    }
    .detailsBlock-3 > h1{
        font: 700 3vmax "Franklin Gothic Medium";
        text-align: center;
    }

    .detailsBlock-3-1{
        flex-direction: column;
    }

    .detailsBlock-3-1-1{
        padding: 2vmax 0;
    }
    .detailsBlock-3-1-1 > button{
        padding: 1.2vmax;
        width: 4vmax;
    }
    .detailsBlock-3-1-1 > input{
        padding: 1.5vmax;
        width: 3vmax;
        font: 400 1.8vmax "Roboto";
    }

    .detailsBlock-3-1 >button:last-child{
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;

    }

    .detailsBlock-3 >p{
        padding: 2.5vmax 0;
        text-align: center;
        font: 400 2vmax "Roboto";
    }
    .detailsBlock-4 {
        font: 500 2.5vmax sans-serif;
        display: block;
        margin-top: 1.5vmax;
        text-align: center;
    }
    .detailsBlock-4 > p{
        font: 300 1.8vmax sans-serif;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .submitReview {
        display: flex;
        justify-content: center;
        align-items: center;
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax auto;
        
    }
    .reviewCard > p {
        font: 600 3vw "Roboto";
      }
      .reviewCardComment {
        font: 300 5vw cursive;
      }

}